<template lang="pug">
#app
  .conf
    .conf__intensity
      input.slider(
        type="range"
        min="1"
        :max="maxWeight"
        v-model="maxIntensity"
        @input="changedMaxIntensity"
      )
      p 
        | Максимальная плотность: 
        input(
          type="number"
          v-model="maxIntensity"
        )
    .conf__radius
      input.slider(
        type="range"
        min="1"
        max="30000"
        v-model="radiusCoef"
        @input="changedRadiusCoef"
      )
      p 
        | Радиус действия точки: 
        input(
          type="number"
          v-model="radiusCoef"
        )
  div.map(ref="map")
</template>

<script>
import Data from './data.js'
const loaded = new Promise((resolve, reject) => {
  window['GMapsInit'] = resolve
})

export default {
  name: 'app',
  data () {
    return {
      map: {},

      heatmapData: [],
      heatmap: {},
      maxIntensity: 200000,
      radiusCoef: 3000,
      maxWeight: 0
    }
  },

  methods: {
    changedMaxIntensity() {
      this.heatmap.setOptions({maxIntensity: this.maxIntensity})
    },
    changedRadiusCoef() {
      this.heatmap.setOptions({radius: this.radiusCoef / (Math.pow(2,(20 - this.map.getZoom())))})
    }
  },

  created() {
    const scriptElement = document.createElement('SCRIPT');
    const apiKey = 'AIzaSyDmPdNB2XAQ-ik_sWNbcorlNPNQgRog6i8'
    const url = `https://maps.googleapis.com/maps/api/js?key=${apiKey}&libraries=visualization&callback=GMapsInit`;
    scriptElement.setAttribute('src', url);
    scriptElement.setAttribute('async', '');
    scriptElement.setAttribute('defer', '');
    document.head.appendChild(scriptElement);


    loaded.then(() => {
      let mapEl = this.$refs.map
      this.map = new google.maps.Map(mapEl, {
        center: new google.maps.LatLng(50.42675, 80.26669),
        zoom: 12,
        mapTypeId: 'roadmap'
      });

      Data.forEach(el => {
        if (el.w > this.maxWeight) {
          this.maxWeight = el.w
        }
        this.heatmapData.push({
          location: new google.maps.LatLng(el.lat, el.lng),
          weight: el.w
        })
      })

      this.heatmap = new google.maps.visualization.HeatmapLayer({
        data: this.heatmapData,
        dissipating: true,
        maxIntensity: this.maxIntensity,
        radius: this.radiusCoef / (Math.pow(2, (20 - this.map.getZoom())))
      });
      this.heatmap.setMap(this.map);

      google.maps.event.addListener(this.map, 'zoom_changed', () => {
        this.changedRadiusCoef()
      });
    })
  }
}
</script>

<style lang="scss">
body, html {
  width: 100%;
  height: 100%;
  padding: 0;
  margin: 0;
}

#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
}

h1, h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}

.conf {
  width: 100%;
  height: 100px;
  display: flex;
}

.conf__intensity,
.conf__radius {
  width: 50%;
}

.slider {
  width: 80%;
}

.map {
  height: 100%;
  width: 100%;
}
</style>



